L.ImageOverlay.Canvas = L.ImageOverlay.extend({
  // includes: L.Mixin.Events,

  options: {
    opacity: 1
  },

  initialize: function (bounds, options) { // (LatLngBounds, Object)
    this._bounds = L.latLngBounds(bounds);
    // compute canvas size with
    console.log(this._bounds.getSouthEast())
    console.log(options.pointBL)
    this.size = {
      x: Math.round((this._bounds.getNorthEast().lng - this._bounds.getNorthWest().lng) * options.nb_columns * 2 / (options.pointTR[1] - options.pointTL[1])) ,
      y: Math.round((this._bounds.getNorthWest().lat - this._bounds.getSouthWest().lat) * options.nb_lines * 2 / (options.pointTL[0] - options.pointBL[0])) 
    }
    this._O =  this._bounds.getNorthWest()
    this._v = (this._bounds.getSouthWest().lat - this._bounds.getNorthWest().lat) / this.size.y
    this._u = (this._bounds.getNorthEast().lng - this._bounds.getNorthWest().lng) / this.size.x
    console.log(this.size) 
    L.Util.setOptions(this, options);
  },

  _initImage: function () {
    var topLeft = this._map.latLngToLayerPoint(this._bounds.getNorthWest()),
        size = this._map.latLngToLayerPoint(this._bounds.getSouthEast())._subtract(topLeft);

    this._image = this.canvas = L.DomUtil.create('canvas', 'leaflet-image-layer');
    this.canvas.width = Math.floor(this.size.x)
    this.canvas.height = Math.floor(this.size.y)
   
    
    this._image.style.width  = size.x;
    this._image.style.height = size.y;

    if (this._map.options.zoomAnimation && L.Browser.any3d) {
      L.DomUtil.addClass(this._image, 'leaflet-zoom-animated');
    } else {
      L.DomUtil.addClass(this._image, 'leaflet-zoom-hide');
    }

    this._updateOpacity();

    //TODO createImage util method to remove duplication
    L.Util.extend(this._image, {
      galleryimg: 'no',
      onselectstart: L.Util.falseFn,
      onmousemove: L.Util.falseFn,
      onload: L.Util.bind(this._onImageLoad, this)
    });
  },
  addMarker(ctx, data) {
    var pos = data.pt
    var x = ((pos[1] - this._O.lng) / this._u)
    var y = ((pos[0] - this._O.lat) / this._v)
   // console.log(x, y)
    ctx.beginPath();
    var gradient = ctx.createRadialGradient(x,y,1, x, y, 2);

    // Add 2 color with different opacity according to value
    var opacity = Math.min( 1, Math.max(0.2, Math.round(Math.abs(data.value) * 10000) / 100))
//    console.log(data.value)
//    // opacity = 0.5
//    console.log(opacity)
    if (data.value > 0) {
      var color = 'rgba(255, 0, 0, ' + opacity +')'
      gradient.addColorStop(0, 'rgba(255, 0, 0, ' + opacity +')')
      gradient.addColorStop(1, 'rgba(255, 0, 0, ' + (opacity * 0.8) + ')')
    } else {
      color = 'rgba(0, 0, 255, ' + opacity +')'
      gradient.addColorStop(0, 'rgba(0, 0, 255, ' + opacity +')');
      gradient.addColorStop(1, 'rgba(0, 0, 255, ' + (opacity * 0.8) +')')
    }
 

    //ctx.arc(x, y, 1, 0, 2 * Math.PI);
    ctx.rect(x - 0.95, y - 0.95, 1.9, 1.9)
    ctx.fillStyle = color
    ctx.fill()
    ctx.closePath()
  },
  download () {
    // var img = this.canvas.toDataURL('image/png')
    window.location.href = this.canvas.toDataURL('image/png')
  },
  addMarkers (data, index) {
    var ctx = this.canvas.getContext('2d')
//    if (data) {
//      this.addMarker(ctx, data[0])
//      this.addMarker(ctx, data[1])
//    }
    for (var i = index; i < data.length - 1 && i < index + 200; i++) {
      this.addMarker(ctx, data[i])
    }
    
    var _this = this
    if (i  < data.length - 1) {
      setTimeout(function () {
        _this.addMarkers(data, i )
      }, 0)
    }
//    var ctx = this.canvas.getContext('2d');
//    var x = 0
//    var y = 0
//    var d = -0.1
//    // Create a radial gradient
//    // The inner circle is at x=110, y=90, with radius=30
//    // The outer circle is at x=100, y=100, with radius=70
//    var gradient = ctx.createRadialGradient(x,x,1, x, x, 2);
//
//    // Add three color stops
//    gradient.addColorStop(0, 'rgba(255, 0, 0, 0.5)');
//    gradient.addColorStop(1, 'rgba(255, 0, 0, 0.1)');
//   // gradient.addColorStop(1, 'green');
//
////     gradient.addColorStop(0, 'white');
////     gradient.addColorStop(1, 'blue');
//
//    ctx.arc(x, x, 2, 0, 2 * Math.PI);
//
//    ctx.fillStyle = gradient;
//    ctx.fill();
//    
//    var x = this.size.x
//    var y = this.size.y
//    var d = -0.1
//    // Create a radial gradient
//    // The inner circle is at x=110, y=90, with radius=30
//    // The outer circle is at x=100, y=100, with radius=70
//    var gradient = ctx.createRadialGradient(x,x,5, x, x, 10);
//
//    // Add three color stops
//    gradient.addColorStop(0, 'rgba(255, 0, 0, 0.5)');
//    gradient.addColorStop(1, 'rgba(255, 0, 0, 0.1)');
//   // gradient.addColorStop(1, 'green');
//
////     gradient.addColorStop(0, 'white');
////     gradient.addColorStop(1, 'blue');
//
//    ctx.arc(x, y, 10, 0, 2 * Math.PI);
//
//    ctx.fillStyle = gradient;
//    ctx.fill();
  },

  _reset: function () {
    var image   = this._image,
        topLeft = this._map.latLngToLayerPoint(this._bounds.getNorthWest()),
        size = this._map.latLngToLayerPoint(this._bounds.getSouthEast())._subtract(topLeft);

    L.DomUtil.setPosition(image, topLeft);

    image.style.width  = size.x + 'px';
    image.style.height = size.y + 'px';
  },

  _onImageLoad: function () {
    this.fire('load');
  }
});

L.imageOverlay.canvas = function (bounds, options) {
  return new L.ImageOverlay.Canvas(bounds, options);
};
module.exports = L.imageOverlay.canvas